	_reportLogs = false;
	_reportInterval: number = 1000 * 60 * 5;
	constructor(options: ConnectionParameters) {
		this.unSyncQueue = [];
		this.lastHeartbeat = Date.now();
		this.isDebug = options.isDebug || false;
		this.isReport = false;
		this.enableReportLogs = options.enableReportLogs || false;
		this.uploadPartEnable = true;
		this.isHttpDNS =
			options.isHttpDNS === undefined ? isHttpDNS : options.isHttpDNS;
		this.heartBeatWait = options.heartBeatWait || 30000;
		this.autoReconnectNumMax = options.autoReconnectNumMax || 5;
		this.refreshDNSIntervals =
			this.autoReconnectNumMax < 5 ? this.autoReconnectNumMax : 5;
		this.delivery = options.delivery || false;
		this.disconnectedReason = undefined;
		this.loginInfoCustomExt = undefined;
		this.dnsArr = [
			'https://rs.easemob.com',
			'https://rs.chat.agora.io',
			'http://59.110.89.59',
			'http://39.97.193.190',
			'http://39.97.193.187',
		];
		this.dnsIndex = 0;
		this.dnsTotal = this.dnsArr.length;
		this.restHosts = [];
		this.restTotal = 0;
		this.restIndex = 0;
		this.hostIndex = 0;
		this.socketHost = [];
		this.hostTotal = 0;
		this.times = 1; // 重联次数
		this.autoReconnectNumTotal = 0;
		this.domain = 'easemob.com';
		this.appKey = options.appKey;
		this.appName = '';
		this.orgName = '';
		this.token = '';
		this.grantType = '';
		this.apiUrl = options.apiUrl || '';
		this.url = options.url || '';
		this.https =
			options.https ||
			(typeof window !== 'undefined' &&
				window.location?.protocol === 'https:');
		// mysnc config
		this.version = '4.8.1';
		this.deviceId = options.deviceId || 'webim';
		this.isFixedDeviceId = options.isFixedDeviceId ?? true;
		this.osType = 16;
		this.useOwnUploadFun = options.useOwnUploadFun || false;
		this.compressType = [0];
		this.encryptType = [0];

		this.clientResource = '';
		this.expiresIn = 0;
		this.expirationTime = 0;
		this.useReplacedMessageContents =
			options.useReplacedMessageContents || false;
		this.logOut = true;
		this.context = {
			jid: {
				appKey: '',
				clientResource: '',
				domain: 'easemob.com',
				name: '',
			},
			userId: '',
			appKey: '',
			status: 0,
			restTokenData: '',
			appName: '',
			orgName: '',
			root: {},
			accessToken: '',
		};
		this._msgHash = {};
		this._msgPromiseHash = {};
		this._queues = [];
		// this.root = {}
		this._load_msg_cache = [];
		this.unMSyncSendMsgMap = new Map();
		this.mr_cache = {};
		this.reconnecting = false;
		this._offlineMessagePullState = OfflineMessagePullState.SYNC_INIT;
		this._offlineMessagePullQueue = [];
		if (
			typeof this.appKey !== 'string' ||
			this.appKey.split('#').length !== 2
		) {
			throw Error(`Illegal appKey: ${this.appKey}`);
		}
		this.devInfos = this.appKey.split('#');
		this.orgName = this.devInfos[0];
		this.appName = this.devInfos[1];
		this.listen = listen.bind(this);
		this.mSync = this.usePlugin(mSync);
		this.eventHandler = this.usePlugin(
			EventHandler
		) as unknown as EventHandler;
		Object.assign(Connection.prototype, contactApis);
		Object.assign(Connection.prototype, groupApis);
		Object.assign(Connection.prototype, chatRoomApis);
		Object.assign(Connection.prototype, presenceApis);
		Object.assign(Connection.prototype, silentModeApis);
		Object.assign(Connection.prototype, threadApis);
		Object.assign(Connection.prototype, translationApis);
		// 初始化DataReport
		this.dataReport = new DataReport({
			appkey: this.appKey,
			org: this.orgName,
			sdkVersion: this.version,
			deviceId: this.deviceId,
			isReport: this.isReport,
		});

		// util.ajax = util.ajax.bind(this);
		let _ajaxUnconventionalErrorTimes = 0;
		// eslint-disable-next-line @typescript-eslint/no-this-alias
		const _self = this;
		Object.defineProperty(utils, 'ajaxUnconventionalErrorTimes', {
			set(value) {
				if (value === 0) {
					return;
				}
				logger.debug(`rest api request fail times: ${value}`);
				_ajaxUnconventionalErrorTimes = value;
				if (
					_ajaxUnconventionalErrorTimes % 5 === 0 &&
					_self.isHttpDNS
				) {
					logger.debug('refresh dns config when rest request fail.');
					getHttpDNS.call(_self, 'refresh', {} as any, {} as RptInfo);
				}
			},
			get() {
				return _ajaxUnconventionalErrorTimes;
			},
		});
		logger.debug(`init SDK: Conn ${this.version} ${platForm.platform}`);
	}
	/**
	 * @hidden
	 */
	usePlugin(this: Connection, plugin: any, name?: string) {
		if (name) {
			this[name] = new plugin(this);
		} else {
			return new plugin(this);
		}
	}

